<template>
  <div id='canteen' class='subPage'>
    <header>
      <img id='back' src='static/assets/back.png' alt='back' @click='back'>
      <div class='canteen-name'>
        <p>{{restaurant.name}}食堂</p>
        <img id="filter-box" src="static/assets/filter.png" alt="filter" @click="filter">
      </div>
    </header>
    <div id='foods'>
      <div v-for='(item, index) in renderMenu' v-bind:index="index" class='food-item'>
        <div class='item-content' @click='$root.learnFood(1, item)'>
          <div class='fd-pic' :style="$root.renderFoodPic(item.photo)"></div>
          <div class='fd-intro'>
            <p>{{item.name}}</p>
            <p>月售357份</p>
            <p>好评率96%</p>
            <p>￥{{item.price}}</p>
          </div>
        </div>
        <div class='cartBox'>
          <img src='static/assets/buy.png' alt='buy'>
        </div>
      </div>
    </div>
    <div id='filter' class='hide-filter' @touchstart='touch' @touchmove='touch' @touchend='touch'>
    </div>
    <transition name='grey'>
      <div v-show='filtered' class='grey' @click.stop='quit'></div>
    </transition>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'canteen',
    data () {
      return {
        tag: false,
        filtered: false,
        y: 0,
        posX: 0,
        posY: 0,
        listenTouch: false
      }
    },
    methods: {
      back: function () {
        this.$router.push({path: '/center'})
      },
      wrapHeader: function () {
        var self = this
        $('#canteen').scroll(function () {
          var y = $(this).scrollTop()
          if (y >= 310) {
            self.tag = self.tag ? self.tag : !self.tag
            if (self.tag) {
              $('.canteen-name').addClass('fixed-name')
              $('.canteen-name p').css('-webkit-transform', 'translateX(' + 100 + 'px)')
              $('.canteen-name p').css('transform', 'translateX(' + 100 + 'px)')
              $('#back').addClass('fixed-back')
            }
          } else if (self.tag) {
            $('#back').removeClass('fixed-back')
            $('.canteen-name').removeClass('fixed-name')
            $('.canteen-name p').css('-webkit-transform', 'translateX(' + 0 + 'px)')
            $('.canteen-name p').css('transform', 'translateX(' + 0 + 'px)')
            self.tag = false
          }
        })
      },
      filter: function () {
        this.filtered = true
        this.listenTouch = true
        $('#filter').addClass('animate-filter')
        $('#filter').addClass('show-filter')
        $('#filter').removeClass('hide-filter')
        setTimeout(function () {
          $('#filter').removeClass('animate-filter')
        }, 200)
      },
      quit: function () {
        this.filtered = false
        this.listenTouch = false
        $('#filter').removeClass('show-filter')
        $('#filter').removeAttr('style')
        $('#filter').addClass('animate-filter')
        $('#filter').addClass('hide-filter')
        setTimeout(function () {
          $('#filter').removeClass('animate-filter')
        }, 200)
      },
      test: function () {
        console.log(1)
      },
      touch: function (ev) {
        if (this.listenTouch) {
          switch (ev.type) {
            case 'touchstart':
              this.posX = ev.targetTouches[0].pageX
              this.posY = ev.targetTouches[0].pageY
              console.log('start')
              break
            case 'touchmove':
              var disx = ev.targetTouches[0].pageX - this.posX
              // var disy = ev.targetTouches[0].pageY - this.posY
              // var temp1 = disx < 0 ? -disx : disx
              // var temp2 = disy < 0 ? -disy : disy
              // if (disx > 0) {
              //   console.log('panRight')
              // } else {
              //   console.log('panLeft')
              // }
              break
            case 'touchend':
              // eslint-disable-next-line
              var disx = ev.changedTouches[0].pageX - this.posX
              break
          }
          if (disx > 0) {
            $('#filter').css('transform', 'translateX(' + disx + 'px)')
          }
          if (disx > 0.03 * this.$root.width) {
            this.filtered = false
            this.listenTouch = false
            $('#filter').removeAttr('style')
            $('#filter').removeClass('show-filter')
            $('#filter').addClass('animate-filter')
            $('#filter').addClass('hide-filter')
            setTimeout(function () {
              $('#filter').removeClass('animate-filter')
            }, 200)
          }
        }
      }
    },
    computed: {
      renderMenu: function () {
        return this.$store.state.menu
      },
      restaurant: function () {
        console.log(this.$store.state.restaurantList[this.$store.state.restaurantIndex])
        return this.$store.state.restaurantList[this.$store.state.restaurantIndex]
      }
    },
    mounted () {
      this.wrapHeader()
    }
  }
</script>

<style scoped>
  #canteen{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: white;
    z-index: 4;
  }
  header{
    width: 100%;
    height: 450px;
    background: #1296db;
    overflow: hidden;
  }
  #back{
    width: 60px;
    height: 60px;
    padding: 40px 5%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
  }
  .fixed-back{
    z-index: 4!important;
  }
  .canteen-name{
    position: relative;
    top: 310px;
    padding: 0 5% 0;
    width: 95%;
    height: 140px;
    z-index: 3;
    overflow: hidden;
    background: #1296db;
  }
  .fixed-name{
    position: fixed!important;
    top: 0;
    left: 0;
  }
  .canteen-name p{
    font-size: 66px;
    line-height: 140px;
    color: white;
    float: left;
    transition: transform .2s;
  }
  #filter-box{
    float: right;
    width: 60px;
    height: 60px;
    padding: 40px 5%;
    background-color: #1296db;
  }
  #foods{
    width: 100%;
    height: auto;
    z-index: 1;
    margin-bottom: 30px;
  }
  #filter{
    width: 80%;
    height: 100%;
    top: 0;
    right: 0;
    position: fixed;
    background: white;
    z-index: 5;
  }
  .animate-filter{
    transform: translate3d(0, 0, 0);
    transition: transform .2s;
  }
  .hide-filter{
    transform: translateX(100%);
  }
  .show-filter{
    transform: translateX(0);
  }
</style>



